import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './Slidenav.css'

class Slidenav extends Component{
	//jsx react-router
	render() {
		return (
			<nav className="nav">
				<div className="menu">
					<Link className="menu-item" to="/user"></Link>
					<Link className="menu-item" to="/postlist"></Link>
					<Link className="menu-item" to="/postedit"></Link>
					<Link className="menu-item" to="/comment"></Link>
					<Link className="menu-item" to="/label"></Link>
					<Link className="menu-item" to="/user"></Link>
				</div>
			</nav>

		)
	}
}

export default Slidenav


